.main-section .tour-box .trip-featured-img-list {
    float: none;
    margin: 20px auto;
    width: 95%;
    border-radius: 0;
    max-width: none;
    max-height: auto;
    height: auto;

    div {
      width: 100%;
      height: 320px;
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center center;

    }
  }

.tour-box {
  h2 {
    font-size: 2em;
    font-weight: bold;
  }
}

.larger {
  font-size: 1.15em;
  font-weight: bold;
}

.summary-text {
  margin-top: 15px;
}
